const state = {
    selectedEvent: {}
}

const mutations = {
    selectEvent(state, event) {
        state.selectedEvent = event
        console.log('state after selecting event', state)
    }
}

const actions = {
    selectEvent({commit}, event) {
        console.log('event in events module', event)
        commit('selectEvent', event)
    }
}

export const events = {
    namespaced: true,
    state,
    mutations,
    actions
}